@import "lib/bourbon/bourbon", "lib/neat/neat";

     
// Sizes
$xlarge: 1500px;
$large: 1000px;
$medium: 768px;
$small: 400px; // for larger smartphones. $small is a bad label
$mega-map-min-width: 548px; // Totally eyeballed it

$xlarge-break: new-breakpoint(min-width $xlarge);
$large-break: new-breakpoint(min-width $large);
$medium-break: new-breakpoint(min-width $medium 4);
$small-break: new-breakpoint(min-width $small 2);
$megamap-break: new-breakpoint(min-width $mega-map-min-width);


$small-wrap: 96%;
$max-content-width: 980px;
$max-article-width: 833px;

// fonts
$em-base: 16px;
$base-font-size: $em-base;
$overdoingit-hed-size: rem(59);
$shouting-hed-size: rem(44);
$major-hed-size: rem(40);
$mid-hed-size: rem(20);
$mid-text-size: rem($base-font-size);
$section-hed-size: rem(26);
$section-hedier-size: rem(28);
$minor-text-size: rem(16);
$minorer-text-size: rem(14);
$compact-text-size: rem(12);
$nav-text-size: $minor-text-size;
$search-text-size: $minorer-text-size;
$base-line-height: 1.45em;
$sans-font-family: Lato, $helvetica;
$serif-font-family: Merriweather, $georgia;

